import { Component } from '@angular/core';
import { IonicPage, NavController, ToastController } from 'ionic-angular';
import { IonicStorageProvider } from '../../providers/base-settings/storage';

@IonicPage()
@Component({
  selector: 'page-dashboard',
  templateUrl: 'dashboard.html'
})
export class DashboardPage {
  // Top Stats
  public properties_count: string = "0";
  public targeted_leads_count: string = "0";
  public leads_count: string = "0";

  // Call Stats
  public calls_count: string = "0";
  public held_calls_count: string = "0";
  public planned_calls_count: string = "0";

  // Meeting Stats
  public meetings_count: string = "0";
  public held_meetings_count: string = "0";
  public planned_meetings_count: string = "0";

  // Site Visit Stats
  public site_visits_count: string = "0";
  public held_site_visits_count: string = "0";
  public planned_site_visits_count: string = "0";

  // Defaults
  public default_image: string;
  public query: string;
  public next_offset: string;
  private sessionToken: string = '';
  private sessionUser: any;
  public searchFilterBasic: any;

  /**
   * Initialize
   */
  constructor(private navCtrl: NavController, private ionStorage: IonicStorageProvider) {
    let env = this;

    // Set Defaults
    env.query = "";

    // Check User Session
    env.checkUserSession();
  }

  /**
   * Check User Session
   */
  checkUserSession() {
    let env = this;

    this.ionStorage.getItems('sessionuser').then(data => {
      console.log(data);
      if(data && data.username) {
        env.sessionUser = data;
        env.sessionToken = data.token;
      } else {
        this.logout();
      }
    });
  }

  /**
   * Go to Election Details Page
   */
  goToElectionDetails(election_id: string) {
    this.navCtrl.push('ElectionDetailsPage');
  }

  /**
   * Logout
   */
  logout() {
    this.ionStorage.removeItem('sessionuser');
    this.navCtrl.setRoot('LoginPage');
  }
}



